import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import axios from "axios";
import FormTransaksiPenjualan from "./Component/FormTranskasiPenjualan";

const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class TransaksiPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listLokasi: [],
      listsales: [],
    };
  }

  componentDidMount() {
    axios.get(server + "storages2?tipeData=LOKASI").then((res) =>
      this.setState({
        listLokasi: res.data,
      })
    );
    axios.get(server + "storages2?tipeData=SALES").then((res) =>
      this.setState({
        listsales: res.data,
      })
    );
  }

  handleSubmit(data) {
    console.log(data);
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <div className="card-body">
              <div className="row">
                <div className="col-lg-4 col-md-4">
                  <div className="form-group">
                    <label htmlFor="">Tanggal</label>
                    <input
                      name="tanggal"
                      type="date"
                      label="Tanggal"
                      className="form-control"
                    />
                  </div>
                </div>
                <div className="col-lg-4 col-md-4">
                  <div className="form-group">
                    <label htmlFor="">No Bon</label>
                    <input
                      name="noBon"
                      type="text"
                      label="No Bon"
                      className="form-control"
                    />
                  </div>
                </div>
                <div className="col-lg-4 col-md-4"></div>
                <div className="col-lg-3 col-md-3">
                  <div className="form-group">
                    <label htmlFor="">Lokasi</label>
                    <select
                      name="lokasi"
                      component="select"
                      className="form-control"
                    >
                      {this.state.listLokasi.map((list) => (
                        <option value={list.kodeLokasi}>
                          {list.namaLokasi}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
                <div className="col-lg-3 col-md-3">
                  <div className="form-group">
                    <label htmlFor="">Sales</label>
                    <select
                      name="sales"
                      component="select"
                      className="form-control"
                    >
                      {this.state.listsales.map((list) => (
                        <option value={list.kodeSales}>{list.namaSales}</option>
                      ))}
                    </select>
                  </div>
                </div>
                <div className="col-lg-3 col-md-3">
                  <div className="form-group">
                    <label htmlFor="">Nama Toko</label>
                    <input
                      name="namaToko"
                      type="text"
                      className="form-control"
                      label="Nama Toko"
                    />
                  </div>
                </div>
                <div className="col-lg-3 col-md-3">
                  <div className="form-group">
                    <label htmlFor="">Customer</label>
                    <input
                      name="customer"
                      type="text"
                      className="form-control"
                      label="Customer"
                    />
                  </div>
                </div>
                <div className="col-lg-12 col-md-12 mt-3">
                  <div className="text-right">
                    <button
                      className="btn btn-primary"
                      data-toggle="modal"
                      data-target="#primary-header-modal"
                    >
                      Tambah Barang
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Transaksi Penjualan
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  Ã—
                </button>
              </div>
              <div className="modal-body">
                <FormTransaksiPenjualan
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

TransaksiPenjualan = reduxForm({
  form: "TransaksiPenjualan",
})(TransaksiPenjualan);
export default connect()(TransaksiPenjualan);
